<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="920px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <div class="clearfix top-filter">
      <el-form ref="form" :model="form" inline :rules="rules">
        <el-form-item prop="needDate" label="需求日期：">
          <el-date-picker v-model="form.needDate" align="center" type="date" value-format="yyyy-MM-dd" style="width: 178px;" class="filter-item" placeholder="请选择" />
        </el-form-item>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :table-height-type="490"
        :selection="false"
        row-key="id"
        :table-column-sort="false"
        :table-data="form.detailList"
        :column-list="columnList"
        :operation-width="120"
        :show-operate="false"
        :refresh-key="refreshKey"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      />
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <div slot="footer">
      <el-button type="primary" @click="confirmDialog">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import _ from 'lodash'
import paginationPage from '@/mixins/paginationPage'
import QueryItemMixin from '@/mixins/queryItem'
import { getListApi } from '@/apis/modules/out-bound/reservation-form-detail'

const INIT_FORM = () => ({
  needDate: '',
  detailList: []
})

export default {
  name: 'MaterialSelect',
  components: {
  },
  mixins: [paginationPage, QueryItemMixin],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: '仓库'
    },
    // 是否多选，如果多选，返回id的数组， 如果单选返回 {id, name}
    multiple: {
      type: Boolean,
      default: true
    },
    // 默认勾选的ids
    selection: {
      type: Array,
      default: () => []
    },
    // 多选状态下的最大选择数量
    max: {
      type: Number,
      default: 0
    },
    ids: {
      type: Array,
      default: () => []
    },
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  dicts: [],
  data() {
    return {
      loading: false,
      refName: 'modelSelectMaterialSelect',
      form: INIT_FORM(),
      rules: {
        needDate: [
          { required: true, message: '请选择需求日期', trigger: 'blur' }
        ]
      },
      columnList: [
        {
          module: 'word',
          prop: 'warehouseSapName',
          label: 'SAP仓库',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialCode',
          label: '物料编码',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '物料名称',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '评估类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inventoryNum',
          label: '库存数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'number',
          prop: 'needNum',
          label: '需求数量',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          isShow: (item) => {
            return true
          },
          getDisplay: (item) => {
            return ''
          },
          disabled: (item) => {
            return false
          },
          setMin: (item) => 0,
          setMax: (item) => item.needMaxNum,
          setPrecision: (item) => 3,
          handleChange: _.throttle((item) => {
          }, 1000),
          showOverflowTooltip: false
        },
        // {
        //   module: 'date',
        //   prop: 'needDate',
        //   label: '需求日期',
        //   align: 'center',
        //   width: '',
        //   minWidth: 160,
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center',
        //   inputWidth: '130',
        //   valueFormat: 'yyyy-MM-dd',
        //   placeholder: '请选择日期',
        //   clearable: true,
        //   isShow: (item) => {
        //     return true
        //   },
        //   getDisplay: (item) => {
        //     return ''
        //   },
        //   setDisabled: (item) => {
        //     return false
        //   },
        //   handleChange: (item) => null,
        //   showOverflowTooltip: false
        // },
        {
          module: 'word',
          prop: 'needDate',
          label: '需求日期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupCode',
          label: '物料组',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupDesc',
          label: '物料组描述',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      refreshKey: 1
    }
  },
  methods: {
    initSelect() {
      this.form.needDate = this.getCurrentDate()
      this.searchInit()
    },
    init() {
      const params = {
        ...this.query,
        reservedNumber: this.extraInfo.billNumber,
        page: {
          ...this.pageInfo
        }
      }
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.form.detailList = list.map(item => {
              const num = item.needNum - item.splitNum
              return {
                ...item,
                needNum: num > item.inventoryNum ? item.inventoryNum : num,
                needMaxNum: num > item.inventoryNum ? item.inventoryNum : num
              }
            })
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    getCurrentDate() {
      const date = new Date()
      const formatObj = {
        y: date.getFullYear(),
        m: date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1,
        d: date.getDate() < 10 ? `0${date.getDate()}` : date.getDate()
      }
      return `${formatObj.y}-${formatObj.m}-${formatObj.d}`
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    confirmDialog() {
      this.$refs.form.validate((valid) => {
        if (!valid) return false
        // const info = this.form.detailList.find(item => item.needNum <= 0)
        // if (info) {
        //   this.$message({
        //     message: `物料[` + info.materialCode + `]需求数量须大于0`,
        //     type: 'error'
        //   })
        //   return
        // }
        this.$emit('submit', { ...this.form })
        this.beforeClose()
      })
    },
    // 更新列表复选框勾选
    handleUpdateTableSelected(type = '') {

    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = INIT_FORM()
      typeof done === 'function' && done()
    },
    onDeptSelectConfirm(list) {
      if (list.length) {
        this.form.usedOffice = list[0].name
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 200px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
