<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="billNumber">
          <el-input v-model="query.billNumber" filterable clearable placeholder="预留单号" style="width: 178px;" class="filter-item" />
        </el-form-item>
        <el-form-item prop="status">
          <el-select v-model="query.status" filterable clearable placeholder="状态" style="width: 178px;" class="filter-item">
            <el-option
              v-for="item in [{name: '未处理', id: 1},{name: '待完成', id: 2},{name: '已完成', id: 3}]"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item prop="project">
          <el-input v-model="query.project" filterable clearable placeholder="项目" style="width: 178px;" class="filter-item" />
        </el-form-item>
        <el-form-item prop="costCenter">
          <el-input v-model="query.costCenter" filterable clearable placeholder="成本中心" style="width: 178px;" class="filter-item" />
        </el-form-item>
        <el-form-item prop="workOrderNumber">
          <el-input v-model="query.workOrderNumber" filterable clearable placeholder="工单号" style="width: 178px;" class="filter-item" />
        </el-form-item>
        <el-form-item prop="createDate">
          <el-date-picker
            v-model="query.createDate"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="创建日期"
          />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
        <el-button
          class="filter-item"
          type="primary"
          icon="el-icon-plus"
          @click="handleAdd"
        >新增</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :key="refreshKey"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="230"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @rowClick="handleRowClick"
        @sortChange="handleSortChange"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" @click="handleDetail(scope.row)">详细</el-button>
          <el-button type="warning" :disabled="scope.row.status===3" @click="handleModify(scope.row)">拆分</el-button>
          <el-button type="warning" :disabled="scope.row.status===3" @click="handleSplit(scope.row)">一键拆分</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />

    <!-- 新增 -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :title="dialogTitle"
      :type="type"
      @submit="init"
    />
    <!-- 一键拆分 -->
    <onekey-split-dialog
      v-model="splitDialog"
      :extra-info="{billNumber: curYLOrderNo}"
      title="一建拆分"
      @submit="splitConfirm"
    />

  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './Dialog'
import OnekeySplitDialog from './OnekeySplitDialog'
import { getListApi, getDeleteApi, getPullListItemApi, getSplitApi } from '@/apis/modules/out-bound/reservation-form'
import tableHeadSet from '@/mixins/tableHeadSet'
import { statusStrMap } from '@/views/in-storage/inspection/status'
import { mapGetters } from 'vuex'

export default {
  name: 'OutBoundReservationForm',
  components: {
    EditDialog,
    OnekeySplitDialog
  },
  mixins: [paginationPage, tableHeadSet],
  data() {
    return {
      title: this.$route.meta.title,
      type: '2',
      loading: false,
      pullList: [],
      query: {
        billNumber: '',
        status: '',
        createDate: '',
        project: '',
        costCenter: '',
        workOrderNumber: ''
      },
      tableData: [],
      columnSets: [
        {
          module: 'status',
          prop: 'status',
          formatter: (status) => {
            return statusStrMap[status - 1]
          },
          statusFormatter: (scope) => {
            const typeMap = {
              '1': 'info',
              '2': 'primary',
              '3': 'warning'
            }
            return scope.status ? typeMap[scope.status] : ''
          }
        },
        {
          prop: 'date',
          sortable: 'custom'
        }
      ],
      statusStrMap: statusStrMap,
      dialogForm: {},
      supplierPullList: [],
      editType: 'add',
      dialogVisible: false,
      splitDialog: false,
      curYLOrderNo: '', // 预留单号
      refreshKey: 0,
      orderBy: '' // 字段排序信息字符串
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    },
    ...mapGetters('app', {
      navbarWarehouseCheckedList: 'navbarWarehouseCheckedList'
    })
  },
  watch: {
    navbarWarehouseCheckedList: {
      handler() {
        this.init()
      },
      deep: true
    }
  },
  mounted() {
    this.init()
    this.getPullList()// 下拉
  },
  methods: {
    init(type) {
      console.log('init method')
      this.loading = true
      const params = type ? { ...this.query, type: this.type } : {
        ...this.query,
        type: this.type,
        ...this.orderBy && { orderBy: this.orderBy },
        page: {
          ...this.pageInfo
        },
        warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    handleSortChange({ column, prop, order }) {
      if (prop !== 'date') return
      console.log('column, prop, order: ', column, prop, order)
      const obj = {
        descending: 'desc',
        ascending: 'asc'
      }
      this.orderBy = order ? `${prop} ${obj[order]}` : ''
      this.orderBy && this.init()
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    // 一键拆分提交
    splitConfirm(form) {
      return new Promise((resolve, reject) => {
        getSplitApi({
          ...form,
          reservedNumber: this.curYLOrderNo

        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: `拆分成功`,
              type: 'success'
            })
            this.init()
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 下拉数据
    getPullList() {
      return new Promise((resolve, reject) => {
        getPullListItemApi({}).then(res => {
          const { code, data } = res
          if (!code) {
            this.pullList = data || []
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 批量删除
    handleDelete(params, item) {
      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        console.log({
          id: params[0],
          dataSources: item.dataSources
        })
        const ids = params || this.$refs.baseTableRef.tableSelectionList.map(i => i.id)
        getDeleteApi({
          id: ids[0],
          dataSources: item.dataSources
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
            this.init()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    // 一键拆分
    handleSplit(row) {
      console.log(row, 'row')
      this.splitDialog = true
      this.curYLOrderNo = row.billNumber
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    // 详情
    handleDetail(item) {
      this.$router.push({
        name: 'OutBoundReservationFormDetail',
        params: {
          id: item.billNumber,
          childrenType: 'detail'
        }
      })
    },
    // 拆分
    handleModify(item) {
      this.$router.push({
        name: 'OutBoundReservationFormSplit',
        params: {
          id: item.billNumber,
          childrenType: 'modify'
        }
      })
    },
    resetForm(formName) {
      this.orderBy = ''
      this.refreshKey++
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
