<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="50"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-drawer">
      <el-form ref="form" :model="form" inline :rules="rules" label-width="120px">
        <!-- <el-form-item label="仓库：" prop="warehouseId">
          <el-select v-model="form.warehouseId" filterable clearable placeholder="请选择" style="width: 178px;">
            <el-option
              v-for="(item,index) in warehousePullList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item> -->
        <el-form-item label="项目" prop="project">
          <el-input v-model.trim="form.project" placeholder="请输入" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="总账科目" prop="ledgerSubjects">
          <el-input v-model.trim="form.ledgerSubjects" placeholder="请输入" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="日期" prop="date">
          <el-date-picker
            v-model="form.date"
            type="date"
            placeholder="请选择"
            value-format="yyyy-MM-dd"
            style="width: 178px;"
          />
        </el-form-item>
        <el-form-item label="工单号" prop="workOrderNumber">
          <el-input v-model.trim="form.workOrderNumber" placeholder="请输入" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="成本中心" prop="costCenter">
          <el-input v-model.trim="form.costCenter" placeholder="请输入" style="width: 178px;" />
        </el-form-item>
      </el-form>
    </el-scrollbar>
    <div class="drawer-footer">
      <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-drawer>
</template>

<script>
import { getAddApi as addApi, getEditApi as updataApi } from '@/apis/modules/out-bound/reservation-form'
import { getWarehouseSapPullListApi } from '@/apis/modules/base/basewmsWarehouse'

const INIT_FORM = {
  // warehouseId: '', // 仓库id
  project: '',
  ledgerSubjects: '',
  date: '',
  workOrderNumber: '',
  costCenter: ''
}

export default {
  name: 'CustomerInfoDialogComs',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM }
      }
    }
  },
  data() {
    return {
      form: INIT_FORM,
      loading: false,
      rules: {
      },
      gradeOpts: [],
      warehousePullList: [] // 仓库下拉列表
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM }
        } else {
          this.form = { ...value }
        }
      },
      immediate: true
    }
  },
  methods: {
    handleConfirm(formName) {
      // 项目 工单号 成本中心 要判断三个值互斥，只需要确保至多有一个值是真
      const a = this.form.project // 项目
      const b = this.form.workOrderNumber // 工单号
      const c = this.form.costCenter // 成本中心
      const arr = [a, b, c]
      const arr1 = arr.filter(c => c)
      if (arr1.length) {
        if (arr1.length > 1) {
          this.$message({
            message: `项目、成本中心、工单号只允许添加一项`,
            type: 'error'
          })
          return
        }
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const api = this.editType === 'add' ? addApi : updataApi
          this.loading = false
          api({
            ...this.form
          }).then(res => {
            const { code } = res
            if (!code) {
              this.$message({
                message: `${this.title}成功`,
                type: 'success'
              })
              this.$emit('submit')
              this.beforeClose()
            }
            this.loading = false
          }).catch(err => {
            console.log(err)
            this.loading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    initSelect() {
      const date = new Date()
      const formatObj = {
        y: date.getFullYear(),
        m: date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1,
        d: date.getDate() < 10 ? `0${date.getDate()}` : date.getDate()
      }
      this.form.date = `${formatObj.y}-${formatObj.m}-${formatObj.d}`
      // this.getWarehousePullList()
    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = Object.assign({}, { ...INIT_FORM })
      typeof done === 'function' && done()
    },
    // 仓库下拉数据
    getWarehousePullList() {
      return new Promise((resolve, reject) => {
        getWarehouseSapPullListApi().then(res => {
          const { code, data } = res
          if (!code) {
            this.warehousePullList = data || []
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    }
  }
}
</script>

<style lang='scss' scoped>
.container{
  width: 860px;
  margin: 0 auto;
  .left{
    width: 640px;
  }
  .right{
    margin-left: 20px;
    width: 180px;
  }
}
::v-deep:focus {
  outline: 0;
}
.scroll-drawer {
  height: calc(100vh - 150px);
  // overflow: auto;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
::v-deep .el-drawer {
  width: 640px;
}
.drawer-footer{
  text-align: right;
}
</style>

